	height: 30px;
	background: rgba(255, 255, 255, 0.6);
	width: 30px;
	border: 0;
	text-align: center;
	cursor: pointer;
	color: #fff;
	border-radius: 50%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
